tyle=\"\n                                                width: 36px;\n                                                height: 36px;\n                                                margin: auto;\n                                            \"\n                                            :src=\"item.icon\"\n                                            alt=\"\"\n                                        />\n                                        <span>{{ item.name }}</span>\n                                    </span>\n                                </span>\n                                <p>₹ {{ item.price }}</p>\n                                <p>{{ item.validity }} months</p>\n                                <div class=\"sub-details-content\">\n                                    <div>100</div>\n                                    <div>Standard</div>\n                                    <div>1</div>\n                                    <div>1</div>\n                                    <div>3</div>\n                                    <div>50-100kb / Event</div>\n                                    <div>50-100kb / Event</div>\n                                    <div>\n                                        <img\n                                            src=\"/assets/login/check.svg\"\n                                            alt=\"\"\n                                        />\n                                    </div>\n                                    <div>\n                                        <img\n                                            src=\"/assets/login/check.svg\"\n                                            alt=\"\"\n                                        />\n                                    </div>\n                                    <div>\n                                        <img\n                                            src=\"/assets/login/check.svg\"\n                                            alt=\"\"\n                                        />\n                                    </div>\n                                    <div>\n                                        <img\n                                            src=\"/assets/login/iconoir_cancel.svg\"\n                                            alt=\"\"\n                                        />\n                                    </div>\n                                </div>\n                                <button\n                                    @click=\"purchase($event, item)\"\n                                    :style=\"item.color\"\n                                    type=\"button\"\n                                >\n                                    Choose Plan\n                                </button>\n                            </div>\n                        </li>\n                    </ul>\n                </div>\n            </div>\n        </div>\n        <!-- <div id=\"section-8\">\n            <div style=\"margin: auto; overflow-x: auto\">\n                <div\n                    v-for=\"(item, index) in priceCards\"\n                    :key=\"index\"\n                    class=\"price-card-container\"\n                >\n                    <div>\n                        <div :style=\"item.color\" class=\"plan\">\n                            {{ item.name }}\n                        </div>\n                    </div>\n                    <div class=\"details-cont\">\n                        <div>/Per Month</div>\n                        <div style=\"text-align: center\">\n                            <span>₹</span><span>{{ item.price }}</span>\n                        </div>\n                        <div>\n                            \"Lorem ipsum dolor sit hjbsdc jsnznxk\n                            amet,consectetur adipiscing elit, sed do eiusmod\n                            tempor incididunt ut labore et dolore magna aliqua.\n                            Ut enim ad minim veniam, quis nostru exercitation.\n                        </div>\n                        <div class=\"pros\">\n                            <img src=\"/assets/Vector3.svg\" alt=\"\" />\n                            <span\n                                >illo inventore veritatis et quasi architecto\n                                beatae vitae</span\n                            >\n                        </div>\n                        <div class=\"pros\">\n                            <img src=\"/assets/Vector3.svg\" alt=\"\" />\n                            <span\n                                >quis nostrum exercitationem ullam corporis\n                                suscipit laboriosam, nisi ut aliquid ex ea\n                                commodi consequatur? Quis autem vel</span\n                            >\n                        </div>\n                        <div class=\"pros\">\n                            <img src=\"/assets/Vector4.svg\" alt=\"\" />\n                            <span\n                                >Duis aute irure dolor in reprehenderit in\n                                voluptate velit esse cillum dolore eu\n                            </span>\n                        </div>\n                        <div class=\"pros\">\n                            <img src=\"/assets/Vector4.svg\" alt=\"\" />\n                            <span\n                                >eius modi tempora incidunt ut labore et dolore\n                                magnam aliquam quaerat voluptatem.</span\n                            >\n                        </div>\n                        <div style=\"text-align: center; margin: 1em 0\">\n                            <button @click=\"purchase($event, item)\" class=\"buy\">\n                                Buy Now\n                            </button>\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div> -->\n        <flashMessage :message=\"message\"></flashMessage>\n    </div>\n</template>\n<script>\nimport flashMessage from \"../FlashMessage.vue\";\n\nexport default {\n    props: [\"host\", \"plans\"],\n    components: {\n        flashMessage,\n    },\n    data() {\n        return {\n            priceCards: this.plans,\n            message: null,\n            paid: false,\n            cont: null,\n        };\n    },\n    methods: {\n        setSplider() {\n            let _this = this;\n            this.$nextTick(function () {\n                _this.cont = new Splide(`#plan-slider`, {\n                    gap: 10,\n                    rewind: true,\n                    autoWidth: true,\n                    pagination: false,\n                    isNavigation: true,\n                    arrows: true,\n                    wheel: false,\n                    breakpoints: {\n                        567: {\n                            arrows: false,\n                            wheel: true,\n                            direction: \"ltr\",\n                            releaseWheel: false,\n                        },\n                    },\n                });\n                _this.cont.mount();\n            });\n        },\n        purchase(e, item) {\n            let _this = this;\n            if (this.paid == true) {\n                window.location.reload;\n                return;\n            }\n            axios\n                .get(route(\"subscribeCreateOrder\", item.id))\n                .then((res) => {\n                    console.log(res);\n                    if (res.data.data?.subscribeable?.name == \"Free\") {\n                        window.location.href = res.data.redirect;\n                        return;\n                    }\n                    _this.checkout(item.price, res.data.data);\n                })\n                .catch((err) => {\n                    console.log(err);\n                });\n        },\n        checkout(price, data) {\n            let _this = this;\n            var options = {\n                key: \"rzp_live_wLIDlEVBFdFkFS\", // Enter the Key ID generated from the Dashboard\n                amount: price * 100, // Amount is in currency subunits. Default currency is INR. Hence, 50000 refers to 50000 paise\n                currency: \"INR\",\n                name: \"HappyWed\",\n                description: \"Test Transaction\",\n                image: \"http://wedding.test/assets/login/logo.png\",\n                order_id: data.order_id, //This is a sample Order ID. Pass the `id` obtained in the response of Step 1\n                handler: function (response) {\n                    let formData = new FormData();\n                    this.paid = true;\n                    formData.append(\"id\", data.id);\n                    formData.append(\"data\", JSON.stringify(response));\n                    axios({\n                        method: \"post\",\n                        url: route(\"subscribeVerifyPayment\"),\n                        data: formData,\n                    })\n                        .then((res) => {\n                            window.location.reload();\n                        })\n                        .catch((err) => {\n                            console.log(err);\n                        });\n                },\n                prefill: {\n                    name: _this.host.name,\n                    email: _this.host.email,\n                    contact: _this.host.mobile,\n                },\n                notes: {\n                    address: \"Razorpay Corporate Office\",\n                },\n                theme: {\n                    color: \"#3399cc\",\n                },\n            };\n            var rzp1 = new Razorpay(options);\n\n            rzp1.on(\"payment.failed\", function (response) {\n                let formData = new FormData();\n                formData.append(\"id\", data.id);\n                formData.append(\"data\", JSON.stringify(response));\n                axios({\n                    method: \"post\",\n                    url: route(\"subscribeVerifyPayment\"),\n                    data: formData,\n                })\n                    .then((res) => {\n                        console.log(\"err\", res, _this);\n                        //_this.message = res.data;\n                        // setTimeout(function () {\n                        //     _this.message = null;\n                        // }, 3000);\n                        //window.location.reload();\n                    })\n                    .catch((err) => {\n                        console.log(err);\n                    });\n            });\n            rzp1.open();\n        },\n    },\n    mounted() {\n        document.onreadystatechange = () => {\n            if (document.readyState == \"complete\") {\n                this.setSplider();\n            }\n        };\n    },\n};\n</script>\n\n<style scoped>\n#section-8 > div:nth-child(1)::-webkit-scrollbar {\n    display: none;\n}\n\n/* Hide scrollbar for IE, Edge and Firefox */\n#section-8 > div:nth-child(1) {\n    -ms-overflow-style: none; /* IE and Edge */\n    scrollbar-width: none; /* Firefox */\n}\n/* price card css start */\n#section-8 {\n    background: #f8f8f8;\n    padding: 3em 0;\n}\n#section-8 > div {\n    display: flex;\n}\n.price-card-container {\n    padding-left: 19px;\n    width: max-content;\n    align-self: center;\n    position: relative;\n}\n.price-card-container > div:nth-child(1) {\n    padding-bottom: 20px;\n    position: absolute;\n}\n.price-card-container > div:nth-child(1)::before {\n    content: \"\";\n    display: block;\n    clip-path: polygon(0 62%, 28% 62%, 28% 100%, 28% 100%);\n    width: 65px;\n    height: 50px;\n    background: #04262e;\n    z-index: 99999;\n    top: 26px;\n    position: absolute;\n    margin-left: -17px;\n}\n.plan {\n    font-family: Josefin Sans;\n    font-style: normal;\n    font-weight: 600;\n    font-size: 18.4072px;\n    line-height: 18px;\n    color: #ffffff;\n    /* background: linear-gradient(90.27deg, #9b3154 1.62%, #6d223b 95.36%); */\n    width: 111px;\n    height: 37px;\n    clip-path: polygon(0 0, 69% 0, 100% 100%, 0 100%);\n    margin-left: -17px;\n    position: absolute;\n    top: 20px;\n}\n.details-cont {\n    background: #ffffff;\n    border-radius: 20px;\n    padding: 1.5em 2em;\n    padding-top: 5em;\n    width: 266px;\n}\n.details-cont > div:nth-child(1) {\n    font-family: Inter;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 11.1324px;\n    line-height: 13px;\n    color: #464646;\n    text-align: right;\n    padding-right: 6em;\n}\n.details-cont > div:nth-child(2) > span:nth-child(1) {\n    font-family: Inter;\n    font-style: normal;\n    font-weight: bold;\n    font-size: 33.6722px;\n    line-height: 41px;\n    color: #000000;\n    padding-right: 10px;\n}\n\n.details-cont > div:nth-child(2) > span:nth-child(2) {\n    font-family: Inter;\n    font-style: normal;\n    font-weight: bold;\n    font-size: 49.4522px;\n    line-height: 60px;\n    color: #000000;\n}\n.details-cont > div:nth-child(3) {\n    font-family: Josefin Sans;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 12px;\n    line-height: 12px;\n    color: #000000;\n    margin-top: 1em;\n    margin-bottom: 1.5em;\n}\n.pros {\n    font-family: Josefin Sans;\n    font-style: normal;\n    font-weight: normal;\n    font-size: 9px;\n    line-height: 9px;\n    color: #5e5e5e;\n    display: flex;\n    margin-bottom: 10px;\n}\n.pros > img {\n    margin-right: 12px;\n    width: 16px;\n    height: 16px;\n}\n.pros > span {\n    vertical-align: middle;\n}\n.buy {\n    font-family: Josefin Sans;\n    font-style: normal;\n    font-weight: 600;\n    font-size: 11.3478px;\n    line-height: 11px;\n    color: #ffffff;\n    border: none;\n    outline: 0;\n    background: linear-gradient(90deg, #9b3154 0%, #6d223b 100%);\n    border-radius: 20px;\n    padding: 12px 28px;\n}\n/* price card css end */\n.subscription-cont {\n    display: flex;\n    margin-bottom: 100px;\n}\n.subs-details {\n    min-width: 280px;\n    width: 30%;\n    margin-right: 10px;\n}\n.subs-details > p {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 24px;\n    line-height: 36px;\n    color: #000000;\n    height: 160px;\n}\n.sub-details-content > div {\n    background: #f6f7f9;\n    border-radius: 3.62305px;\n    padding: 9px 22px;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 15.3063px;\n    line-height: 23px;\n    color: #5b5252;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    height: 41px;\n}\n.sub-details-content > div:nth-child(even) {\n    background: #ffffff;\n}\n.single-sub-page {\n    width: 200px;\n}\n.single-sub-page > span:nth-child(1) {\n    position: relative;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    height: 110px;\n}\n.single-sub-page > span:nth-child(1) > img {\n    position: absolute;\n    top: 0;\n    left: 11%;\n    width: 150px;\n    height: 110px;\n    z-index: 99;\n}\n.single-sub-page > span:nth-child(1) > span {\n    z-index: 999;\n    display: flex;\n    justify-content: center;\n    flex-direction: column;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 600;\n    font-size: 20.4084px;\n    line-height: 31px;\n    color: #ffffff;\n}\n.single-sub-page > p:nth-child(2) {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 31.5434px;\n    line-height: 47px;\n    text-align: center;\n    color: #5b5252;\n    margin-bottom: 7px;\n}\n.single-sub-page > p:nth-child(3) {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 10.4717px;\n    line-height: 16px;\n    color: #ada2a2;\n    text-align: center;\n    margin-bottom: 7px;\n}\n#plan-slider {\n    width: 70%;\n}\n.single-sub-page > button {\n    outline: none;\n    border: none;\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 12.225px;\n    line-height: 18px;\n    color: #ffffff;\n    margin-top: 15px;\n    /* border: 0.670952px solid #00a861; */\n    border-radius: 27.1729px;\n    width: 100%;\n    padding: 14px;\n}\n</style>\n"],"sourceRoot":""}]);
